import React from "react";
import { IconButton } from "@material-ui/core";
import {
	Add,
	Save,
	Delete,
	Undo,
	Redo,
	Menu,
	Refresh,
} from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";

import ViewSelection from "./ViewSelection";
import { MODEL_TYPE } from "./../constants";

const useStyles = makeStyles({
	toolbarContainer: {
		display: "flex",
		flexWrap: "wrap",
		width: "100%",
		height: "inherit",
		backgroundColor: "rgb(41, 56, 70)",
		padding: "0.5rem 0.75rem",
		".modern-dark &": {
			backgroundColor: "#323232",
		},
	},
	toolItemView: {
		color: "#fff",
		padding: "5px 10px",
	},
	crudIcon: {
		height: "1em",
		width: "1em",
		fontSize: 18,
		cursor: "pointer",
	},
	selectionContainer: {
		flex: 1,
		display: "flex",
		alignItems: "center",
		flexWrap: "wrap",
	},
	toolbarActions: {
		display: "flex",
		alignItems: "center",
	},
	disableBtn: {
		color: "#555 !important",
	},
});

function isStudioView(view) {
	if (view && view.xmlId && view.xmlId.indexOf("studio-") === 0) {
		return true;
	}
	return false;
}

function ToolbarButton(props) {
	const classes = useStyles();
	return (
		<IconButton
			className={classes.toolItemView}
			classes={{ disabled: classes.disableBtn }}
			{...props}
		/>
	);
}

function Toolbar({
	update,
	saveView,
	modelType,
	saveCustomFieldView,
	removeView,
	model,
	onNew,
	reset,
	...props
}) {
	const classes = useStyles();
	const save = React.useCallback(() => {
		saveView();
		if (modelType === MODEL_TYPE.BASE) {
			saveCustomFieldView();
		}
	}, [saveView, saveCustomFieldView, modelType]);
	const isRemoveDisabled = !(
		props.extensionView ||
		isStudioView(props.selectedView) ||
		(modelType === MODEL_TYPE.CUSTOM && Boolean(props.customModel))
	);
	const isNewDisabled = modelType === MODEL_TYPE.BASE && !model;
	const isSaveDisabled = Object.keys(props.errorList).length > 0;

	return (
		<div className={classes.toolbarContainer}>
			<div className={classes.toolbarActions}>
				<ToolbarButton
					className={classes.toolItemView}
					onClick={props.onSidePanelToggle}
				>
					<Menu className={classes.crudIcon} />
				</ToolbarButton>
				{modelType !== MODEL_TYPE.BASE && (
					<ToolbarButton
						className={classes.toolItemView}
						onClick={onNew}
						disabled={isNewDisabled}
					>
						<Add className={classes.crudIcon} />
					</ToolbarButton>
				)}
				<ToolbarButton
					className={classes.toolItemView}
					onClick={save}
					disabled={isSaveDisabled}
				>
					<Save className={classes.crudIcon} />
				</ToolbarButton>
				<ToolbarButton
					className={classes.toolItemView}
					onClick={removeView}
					disabled={isRemoveDisabled}
				>
					<Delete className={classes.crudIcon} />
				</ToolbarButton>
			</div>
			<div className={classes.selectionContainer}>
				<ViewSelection
					model={model}
					update={update}
					modelType={modelType}
					reset={reset}
					{...props}
				/>
			</div>
			<div className={classes.toolbarActions}>
				<ToolbarButton
					className={classes.toolItemView}
					onClick={() => props.refresh()}
					disabled={!props.customModel && !model}
				>
					<Refresh className={classes.crudIcon} />
				</ToolbarButton>
				<ToolbarButton
					className={classes.toolItemView}
					onClick={() => props.undo()}
					disabled={props.pastLength <= 0}
				>
					<Undo className={classes.crudIcon} />
				</ToolbarButton>
				<ToolbarButton
					onClick={() => props.redo()}
					disabled={props.futureLength <= 0}
				>
					<Redo className={classes.crudIcon} />
				</ToolbarButton>
			</div>
		</div>
	);
}

export default React.memo(Toolbar);
